@font-face {
	font-family: $font-family;
	src: url("#{$font-path}/Yekan.eot");
	/* IE9 Compat Modes */
	src: url("#{$font-path}/Yekan.eot?#iefix") format('embedded-opentype'),
	/* IE6-IE8 */
	url("#{$font-path}/Yekan.woff2") format('woff2'),
	/* Modern Browsers */
	url("#{$font-path}/Yekan.woff") format('woff'),
	/* Modern Browsers */
	url("#{$font-path}/Yekan.ttf") format('truetype');
	/* Safari, Android, iOS */
	font-weight: $font-weight;
	font-style: $font-style;
	text-rendering: optimizeLegibility;
	font-display: auto;
}